<template>
  <div class="update-name-container">
    <van-nav-bar title="" left-text="" @click-left="$emit('close')">
      <div slot="right">
        <van-button class="save-btn" :disabled="name !== localName.trim() ? false : true" @click="onSaveName"></van-button>
      </div>
    </van-nav-bar>
    <van-field class="username-dialog" v-model="localName" autofocus placeholder="" maxlength="7" show-word-limit />
    <!-- <span class="notice"> 1-7 </span> -->
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateName',
  props: {
    name: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      localName: this.name
    }
  },
  methods: {
    // 
    async onSaveName() {
      this.$toast.loading({
        message: '...',
        forbidclick: true,
        duration: 0 // 
      })
      try {
        await updateUserProfile({
          name: this.localName // 
        })

        // 
        this.$emit('close')
        // 
        this.$emit('update-name', this.localName)

        this.$toast.success({
          message: '',
          forbidclick: true
        })
      } catch (err) {
        if (err && err.response && err.response.status === 409) {
          this.$toast('')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-name-container {
  /deep/.van-nav-bar {
    background-color: unset;
    .van-nav-bar__title {
      color: #333;
    }
    .van-nav-bar__left {
      .van-nav-bar__text {
        color: #6e6e6e;
      }
    }
    .save-btn {
      background-color: unset;
      border: none;
      width: 60px;
      position: absolute;
      top: 0px;
      right: 0;
      color: #3296fa;
      font-size: 15px;
    }
  }
  .username-dialog {
    padding-bottom: 0;
    /deep/.van-field__body {
      height: 35px;
      font-size: 15px;
      border-bottom: 1px solid #dcdfe6;
      padding: 0 10px;
    }
    /deep/.van-field__word-limit {
      position: absolute;
      top: 10px;
      right: 8px;
      margin-top: 0;
    }
  }
  // .notice {
  //   font-size: 13px;
  //   color: #e0e0e0;
  //   margin-left: 15px;
  // }
}
</style>
