<template>
  <div class="update-avatar-container">
    <div class="preview-image-warp"><img class="preview-image" ref="preview-image" :src="image" alt="" /></div>
    <van-nav-bar class="toolbar" left-text="" right-text="" @click-left="$emit('close')" @click-right="onConfirm"></van-nav-bar>
  </div>
</template>

<script>
import { updateUserPhoto } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'UpdateAvatar',
  props: {
    file: {
      required: true
    }
  },
  data() {
    return {
      image: window.URL.createObjectURL(this.file), // 
      cropper: null // 
    }
  },
  mounted() {
    if (this.cropper) {
      this.cropper.replace(this.previewImage)
      return
    }

    // DOM
    const image = this.$refs['preview-image']
    // ()
    // ()1..replace() 2. .destroy()
    this.cropper = new Cropper(image, {
      // aspectRatio  1/1  1
      // aspectRatio: 1 / 1
      aspectRatio: 1,
      //  viewMode: 1 
      viewMode: 1,
      //  none/move
      dragMode: 'move',
      // 
      background: false
    })
  },
  methods: {
    // 
    getCroppedCanvas() {
      return new Promise((resolve) => {
        this.cropper.getCroppedCanvas().toBlob((blob) => {
          resolve(blob)
        })
      })
    },
    // 
    async onConfirm() {
      this.$toast.loading({
        message: '...',
        forbidclick: true,
        duration: 0 // 
      })

      const file = await this.getCroppedCanvas()

      // Content-Typemultipart/form-dataFormData
      const fd = new FormData()
      // fd.append('photo', )
      fd.append('photo', file)

      const { data } = await updateUserPhoto(fd)

      // 
      this.$emit('close')
      // 
      this.$emit('update-avatar', data.data.photo)

      this.$toast.success({
        message: '',
        forbidclick: true
      })
    }
  }
}
</script>
0

<style lang="less" scoped>
.update-avatar-container {
  .toolbar {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: unset;
  }
  .preview-image-warp {
    margin-top: 20px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    .preview-image {
      display: block;
      max-width: 100%;
    }
  }
}
</style>
