<template>
  <div class="update-birth-container">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit('close')"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
import dayjs from 'dayjs'

export default {
  name: 'UpdateBirth',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      minDate: new Date(1980, 0, 1), // 
      maxDate: new Date(), // ()
      // maxDate: new Date(2050, 0, 1), // ()
      currentDate: new Date(this.value) // 
    }
  },
  methods: {
    // 
    async onConfirm() {
      this.$toast.loading({
        message: '...',
        forbidclick: true,
        duration: 0 // 
      })

      const date = dayjs(this.currentDate).format('YYYY-MM-DD')

      await updateUserProfile({
        birthday: date // 
      })
      // 
      this.$emit('close')
      // 
      this.$emit('input', date)

      this.$toast.success({
        message: '',
        forbidclick: true
      })
    }
  }
}
</script>

<style lang="less" scoped>
.update-birth-container {
  /deep/.van-picker__confirm {
    color: #3296fa;
  }
}
</style>
